body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.map {
  height: 93vh;
}

.smallMap {
  height: 45vh;
}

input, textarea {
  display: block;
}

label {
  margin-top: 10px;
}

.markerButtons {
  width: 80%;
  padding: 5px;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  border-top: 1px solid black;
  text-align: center;
}

.markerButtons button{
  margin: 5px;
}

.updateModal input, .updateModal textarea{
  width: 100%;
}

.navigation {
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 5px;
}

.navigation button{
  margin-right: 5px;
}